.search-bar {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .search-bar input {
    padding: 10px;
    width: 60%;
    border: 2px solid #ff6b6b;
    border-radius: 5px;
    font-size: 16px;
  }
  
  .search-bar button {
    margin-left: 10px;
  }
  